@import url(https://fonts.googleapis.com/css?family=Spartan:400,500&display=swap);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{box-sizing:border-box}*,*:before,*:after{box-sizing:inherit}.App{text-align:center;width:100%}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}html,.dark{background-color:#002b36;color:#839496}html *,.dark *{color:#839496}html h1,html h2,html h3,html h4,html h5,html h6,.dark h1,.dark h2,.dark h3,.dark h4,.dark h5,.dark h6{color:#93a1a1;border-color:#839496}html a,html a:active,html a:visited,.dark a,.dark a:active,.dark a:visited{color:#93a1a1}.light{background-color:#fdf6e3;color:#657b83}.light *{color:#657b83}.light h1,.light h2,.light h3,.light h4,.light h5,.light h6{color:#586e75;border-color:#657b83}.light a,.light a:active,.light a:visited{color:#586e75}.landing{height:100vh;display:flex;flex-direction:column;flex-wrap:nowrap;justify-content:center;align-items:center;font-family:"Spartan";font-size:2rem;font-weight:extra-bold;grid-template-rows:3fr 1fr 1fr 3fr}.landing .text{display:inherit;flex-direction:column;align-items:center;justify-content:center;flex-grow:100}.landing p{display:flex}.landing p:nth-child(1){grid-row:2 / 3}.landing p:nth-child(2){grid-row:3 / 4}.landing button{background-color:#002b36;border:solid #d33682 2px;border-radius:1px;font-size:20px;font-weight:400;color:#d33682;display:flex;flex-direction:row;align-self:center;justify-self:end}.landing button i{color:#d33682;font-size:33px}.accent{color:#d33682}

